import http from '../../common/http.js'

export default {
	data() {
		return {
			imgUrl: http.imgUrl,
			list: [],
			goods_id: null,
			goods: '',
			shop: {},
		}
	},
	onLoad: function(e) {
		console.log(e)
		this.goods_id = e.goods_id

	},
	onShow: function() {
		this.allRequest()
	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {
			this.getGoodsDetail()
		},
		//获取商品详情
		getGoodsDetail: function() {
			let _this = this
			let opts = {
				url: 'shop/getGoodsDetail',
				method: 'post'
			};
			let param = {
				city_id: '370700',
				user_id: '1629',
				// goods_id: 1566
				goods_id: _this.goods_id
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				_this.goods = res.data.data.goods
				_this.shop = res.data.data.goods.shop
				let picArr = res.data.data.goods.many_pic
				let list = []
				for (let i in picArr) {
					let itemObj = {
						image: _this.imgUrl + picArr[i]
					}
					list.push(itemObj)
				}
				_this.list = list
				console.log(list)
				console.log(Boolean(_this.goods.goods_option))
				console.log(_this.goods.goods_option)

			}, error => {
				console.log(error);
			})
		},
		goIndex: function() {
			uni.switchTab({
				url: '../index/index'
			})
		},
		//拨打电话
		callPhone: function() {
			let _this = this
			uni.showModal({
				title: '提示',
				content: "您确定要拨打" + _this.shop.mobile + "吗",
				success: function(res) {
					if (res.confirm) {
						uni.makePhoneCall({
							phoneNumber: _this.shop.mobile
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}

			})
		},
		toBuy:function(){
			uni.navigateTo({
				url:'../order/bendiCreateOrder'
			})
		}




	}
}
